@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: "Jost", sans-serif;
}

body {
    background: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: "Jost", sans-serif;
}

html {
    font-size: 100%;
    height: 100%;
    background: #eee;
}


p{
    padding-top: 25%;
    font-size: 30px;
    font-weight: 600;
}

.box {
    width: 30vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
    min-width: 20rem;
    min-height: 35rem;
    padding-top: 20px;
}

.wave {
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: #fff;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
}

.wave.-three {
    animation: drift 7000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}

.wave.-two {
    animation: drift 7000ms infinite linear;
    opacity: 0.1;
    z-index: 3 !important;
}

.box::after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0, 0, 0);
}

@keyframes drift {
    from {
        transform: rotate(0deg);
    }
    from {
        transform: rotate(360deg);
    }
}

.info {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
}

.location {
    margin-top: -5.5rem;
    text-align: center;
    font-weight: 800;
    font-size: 5rem;
    text-transform: capitalize;
}


@keyframes rotates {
    from {
        transform: translateX(-0.5rem);
    }
    from {
        transform: translateX(0.5rem);
    }
}

#date {
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem; 
    font-weight: 300;
    text-transform: uppercase;
}

.temp {
    margin-top: 5rem;
    text-align: center;
    font-size: 3rem;
    color: rgba(3, 3, 143, 0.74);
}
.tempmin_max {
    text-align: center;
    margin-top: 2rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #0847bb;
}





@media (max-width: 600px) {
    body {
        background: linear-gradient(90deg, rgba(21,100,154,0.9640231092436975) 11%, rgba(108,165,196,1) 63%);
    }   
    .box {
        width: 90vw;
        height: 80vh;
        box-shadow: 0 -5px 3px -3px black, 0 5px 3px -3px black;
    }

    .wave {
        top: 85%;
    }


    .info {
        font-size: 1.5em;
    }
}

@media (max-width: 500px) {
    body {
        background: linear-gradient(90deg, rgba(21,100,154,0.9640231092436975) 11%, rgba(108,165,196,1) 63%);
    }   
    .box {
        height: 80vh;
        width: 95vw;
        box-shadow: 0 -5px 3px -3px black, 0 5px 3px -3px black;
    }

    .wave {
        top: 115%;
    }
}

body > span {
    width: 100vw;
    text-align: center;
    color: grey;
}

.inputData {
    padding-top: 15px;
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inputField {
    width: 60%;
    height: 80%;
    margin: auto;
    border-radius: 5rem;
    padding: 1rem 1rem;
    outline: none;
    font-size: 1.5rem;
}

